html{
    background: #F6D3E6B2;
    text-align: center;
}
.box{
    display:flex;
   justify-content: center;
   margin-top: 5em;
}
.box1 .heading{
    background-color: #c9c3c3b2;
    padding:1em 3em;
    border-radius: 40px;
}
.box1{
    background:rgb(250, 248, 248);
    border-radius:40px;
    padding:0 1.5em 5em;
    position: relative;
    top:5.5em;
    height: 23em;
}
.boxv1{
    display:flex;
    gap:2em;
    font-size: .6em;    
}
.boxv{
    background: #f9f5f5;
    box-shadow: 0.01em 0.01em 2em 0 rgb(210, 207, 207);
    padding:0 .5em;
    margin-top: .5em;
    border-radius: 30px;
}
.boxv img{
    width:1.7cm;
    border-radius: 40px;
}
#boxi4{
    position:relative;
    top:4em;
    width:7em
}
#boxi3{
    position:relative;
    top:1.6em;
    width: 7em;
}
#boxi2{
    position:relative;
    top:1.8em;
    width: 7em;
}
#boxi1{
    position:relative;
    bottom:1em;
    width: 7em;
}
.box2 img{
    border-radius: 40px;
    position: relative;
    bottom: 1em;
    width: 17em;
    height: 35em;
}
.box3 img{
    position: relative;
    top:3em;
    width: 15em;
    height: 30em;
    border-radius: 40px;
}
.box4 img{
    position: relative;
    top:4.5em;
    width: 16em;
    height:28em;
    border-radius: 40px;
}
#boxid:hover{
    transform:scale(1.1);
}
#boxid:active{
    transform:translate(0,-5em);
}
.landing h3{
    text-align: center;
}